@import "@/theme/index.scss";

.star {
  padding-right: 8px;
  padding-bottom: 14px;
}

.slider {
  position: relative;

  @include screen-md {
    padding-left: 15%;
    padding-right: 10%;
  }

  &__info {
    width: 65%;
    margin: 0 auto;
    @include font-size(400, 12px, 24px);
    color: #90a3b4;

    @include screen-md {
      width: 100%;
      @include font-size(400, 16px, 32px);
      padding-bottom: 20px;
    }
  }
  &__star {
    padding-left: 10%;
  }
}

.user-info {
  display: flex;
  width: 65%;
  margin: 0 auto;

  @include screen-md {
    width: 100%;
  }

  &__icon {
    padding-right: 15px;
  }
}

.user-name {
  @include font-size(700, 10px, 24px);
  color: $orang-strong;

  @include screen-md {
    @include font-size(700, 20px, 24px);
    padding-bottom: 4px;
  }
}

.user-prof {
  color: #90a3b4;
  @include font-size(400, 8px, 18px);

  @include screen-md {
    @include font-size(400, 16px, 28px);
  }
}

.paws {
  position: absolute;
  left: -40px;
  top: -40px;
  z-index: 3000;
}

.splide__pagination li {
  margin-bottom: -70px;
}

.splide__pagination__page.is-active {
  background: $orang-strong;
}

.splide__arrow {
  background: $orang-strong;
}

.splide__arrow svg {
  fill: $black-light;
}
