@import "@/theme/index.scss";

.videoBlock {
  max-width: 500px;
  width: 100%;
  position: relative;

  &__video {
    width: 100%;
  }
}

.play {
  position: absolute;
  top: 40%;
  left: 40%;
  z-index: 3000;
  width: 66px;
  height: 66px;
  border: 2px solid $white;
  border-radius: 50%;
  @include position-center(flex, column, center, center);
}

.bookmark {
  position: absolute;
  right: -10px;
  bottom: -25px;
  width: 162px;
  height: 131px;
}
