@import "@/theme/index.scss";

.profileInfoResumeArticle {
  width: 100%;
  padding-top: 34px;
  padding-bottom: 34px;
  background: $white;
  box-shadow: 0px -1.24046px 15.7716px rgba(0, 0, 0, 0.0945694),
    0px -0.608097px 7.73152px rgba(0, 0, 0, 0.0754306),
    0px -0.240442px 3.05705px rgba(0, 0, 0, 0.0527031);
  border-radius: 25px;
  &__title {
    margin-left: 34px;
    margin-right: 34px;
    color: black;
    @include font-size(600, 20px, 120%);
    padding-bottom: 15px;
    border-bottom: 1px solid #cccccc;
  }
  &__text {
    margin-left: 34px;
    margin-right: 34px;
    color: #393939;
    @include font-size(400, 14px, 24px);
    display: flex;
    justify-content: space-between;
  }
}
.profileInfoResume {
  padding-top: 34px;
  padding-bottom: 66px;
}
.resumeText {
  width: 20%;
}
