@import 'scss-reset/_reset.scss';

.wrapper {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  outline: none;
  border: none;
  text-decoration: none;
  max-width: 100%;
  background: $background;
}

a {
  text-decoration: none;
}

.main-container {

  padding-left: 7.6%;
  padding-right: 7.6%;
}
span {
  color: $orang;
}